@import '../../styles/mixins';
@import '../../styles/typography';

.porftolio-cart {
  height: 310px;
  width: 530px;
  margin-top: 54px;

  cursor: pointer;

  @include lg-max {
    height: 410px;
  }

  .img-wrap {
    position: relative;
    height: 100%;
    overflow: hidden;

    &:hover .abstract {
      bottom: 0;
    }

    img {
      object-fit: cover;
    }

    .abstract {
      transition: var(--transition);
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: var(--color-light);
      bottom: -295px;
      left: 0;
      @include flex(column, flex-start, center);
      text-align: left;
      padding: 20px;
      border-top: 15px solid var(--color-main);

      @include lg-max {
        bottom: -395px;
      }

      h3 {
        z-index: 9;
        text-transform: uppercase;
        font-family: var(--header-font);
        letter-spacing: 1px;
      }

      p {
        padding: 10px 0 30px 0;
      }
    }
  }
}
